@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#dot{
  height: 27px;
  width: 28px;
  background-color: #6C5CE7;
  border-radius: 50%;
}
/* ================NAVE BAR================ */

.rounded-pill {
  height: 120px;
  padding-right: 20%;
}
.nav-item {
  font-family: Mulish;
  font-size: 16px;
 line-height: 32px;
 color: #6C5CE7;
  padding-left: 2.5rem;
  font-weight: 400;
}
.navbar-nav {
  margin-left: auto;
}
.navbar {
  background-color: #fff !important ;
}
.btn-get-started {
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 7px 25px;
  border-radius: 50px;
  border: 2px solid #3498db;
  transition: 0.5s;
  background: #3498db;
  color: #fff;
  text-decoration: none;
}
.btn-get-started:hover {
  background: #091b5373;
  color: rgb(243, 230, 230);
}
