.brand_name {
  /* Let’s Find that Best Candidate... */
  font-family: Mulish;
  font-style: normal;
  font-weight: 600;
  font-size: 48px;
  line-height: 60px;
  letter-spacing: -1px;

  /* Black */
  color: #2d3436;
}
#header .btn-get-started {
  margin-bottom: 10px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 60px;
  border-radius: 96px;
  border: 2px solid #3498db;
  transition: 0.5s;
  background: #6c5ce7;
  font-weight: bold;
  font-size: 24px;
  line-height: 36px;
  color: #fff;
  letter-spacing: 1.5px;
  text-decoration: none;
}
#header .btn-get-started:hover {
  background: #dedde7;
  color: rgb(102, 20, 20);
}

